/* ==========================================================================
   WooCommerce Dropdown Cart styles - begin
   ========================================================================== */

.qodef-shopping-cart-holder {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    height: 100%;
    margin: 0 40px 0 10px;

    .touch & {
        cursor: pointer;
    }

    .qodef-shopping-cart-inner {
        position: relative;
        display: table;
        height: 100%;
    }

    .qodef-header-cart {
        position: relative;
        display: table-cell;
        vertical-align: middle;
        line-height: 1;

        .qodef-cart-icon {
            position: relative;
            display: inline-block;
            vertical-align: top;
        }
        
        .qodef-cart-number {
            font-size: 15px;
            font-weight: 700;
            position: absolute;
            top: 25px;
            left: 21px;
        }

        &.qodef-header-cart-icon-pack {
            
            .qodef-cart-icon {
                font-size: 20px;
                line-height: 1em;
    
                * {
                    display: block;
                    line-height: inherit;
        
                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }
            }
        }

        &.qodef-header-cart-svg-path {
            
            .qodef-cart-icon {

                > * {
                    vertical-align: middle;
                }
                
                > *, 
                path {
                    fill: currentColor;
                }
            }
        }
    }

    &:hover {

        .qodef-shopping-cart-dropdown {
            top: 100%;
            opacity: 1;
            visibility: visible;
        }
    }
}

.qodef-dark-header .qodef-page-header > div:not(.qodef-sticky-header):not(.fixed) .qodef-shopping-cart-holder .qodef-header-cart {
    color: $header-dark-color;

    &:hover {
        color: $header-dark-hover-color;
    }
}

.qodef-light-header .qodef-page-header > div:not(.qodef-sticky-header):not(.fixed) .qodef-shopping-cart-holder .qodef-header-cart {
    color: $header-light-color;

    &:hover {
        color: $header-light-hover-color;
    }
}

.qodef-shopping-cart-dropdown {
    position: absolute;
    top: calc(100% + 50px);
    left: auto;
    right: 0;
    width: 272px;
    background-color: #fff;
    box-shadow: 1px 1px 11px 0px rgba(0, 0, 0, 0.09);
    text-align: left;
    z-index: 1000;
    opacity: 0;
    visibility: hidden;
    box-sizing: border-box;
    @include qodefTransition(all 0.35s cubic-bezier(.53,.01,.18,1));

    ul {
        margin: 44px 28px;
        padding: 0;

        li {
            @include qodefTableLayout();
            padding: 6px 0;
            box-sizing: border-box;

            &.qodef-empty-cart {
                border-bottom: 0;
                font-size: 16px;
                line-height: 20px;
                color: #717171;
            }

            &:first-child {
                padding-top: 0;
            }

            &:last-child {
                padding-bottom: 0;
            }
        }
    }

    .qodef-item-image-holder {
        position: relative;
        display: table-cell;
        height: 100%;
        width: 43px;
        vertical-align: top;

        img {
            display: block;
        }
    }

    .qodef-item-info-holder {
        @include qodefTableCellLayout();
        vertical-align: top;
        padding-left: 10px;

        .qodef-product-title {
            display: block;
            margin: -2px 0 0 0;
            padding: 0 20px 0 0;
            box-sizing: border-box;
        }

        .remove {
            position: absolute;
            top: -7px;
            right: -5px;
            font-size: 18px;
	        line-height: 1;

            span {
                display: block;
                line-height: inherit;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }

        .qodef-quantity {
            display: block;
            margin: 0 1px 0;
            font-size: 15px;
            line-height: 14px;
        }

        .amount {
            display: inline-block;
            margin: 3px 0 0;
            color: $default-text-color;
        }
    }

    .qodef-cart-bottom {
        padding: 17px 1px 0;

        .qodef-subtotal-holder {
            position: relative;
            display: table;
            width: 100%;
            margin: 0 0 16px;

            > * {
                display: table-cell;
                vertical-align: top;
                color: $default-heading-color;
                line-height: 20px;
            }

            .qodef-total {
                font-size: 19px;
                font-weight: 500;
            }

            .qodef-total-amount {
                text-align: right;
	            font-size: 16px;
                color: $default-text-color;
                margin: -2px 0 0 0;
                display: block;
            }
        }
        
        .qodef-btn {
            width: 100%;
	        text-align: center;
        }
        
        .qodef-checkout {
            margin: 12px 0 0 0;
        }
    }
}

.qodef-vertical-menu-area {

    .qodef-shopping-cart-dropdown {
        display: none !important;
    }
}
/* ==========================================================================
   WooCommerce Dropdown Cart styles - end
   ========================================================================== */