/* ==========================================================================
   WooCommerce Buttons styles - begin
   ========================================================================== */

.woocommerce-page .qodef-content,
div.woocommerce {
    
    a.button,
    a.added_to_cart,
    input[type="submit"],
    button[type="submit"]:not(.qodef-search-submit),
    .wc-forward:not(.added_to_cart):not(.checkout-button) {
        @include qodefButtonDefaultStyle();
        @include qodefButtonSize();
        @include qodefButtonSolidColor();
        cursor: pointer;
        z-index: 3;

        &:hover {
            @include qodefButtonSolidHoverColor();
        }
    }
    
    a.button.added {
        display: none;
    }

    .single_add_to_cart_button {
        padding: 10px 48px 10px 48px;
        @include qodefTransition(.2s .15s !important);

        &:hover {
            color: #fff !important;
            background-color: #000 !important;
            border: 1px solid transparent !important;
            padding: 10px 38px 10px 58px !important;
            @include qodefTransition(.5s cubic-bezier(.68,.04,0,.93) !important);

            &:before {
                width: 25px;
                @include qodefTransition(width .2s ease-out);
                
            }

            &:after {
                transform: rotate(45deg) scale(1) translateY(-0.5px);
                @include qodefTransition(all .15s .1s);
            }
        }

        &:before {
            content: '';
            display: inline-block;
            position: absolute;
            height: 1px;
            width: 18px;
            background-color: #fff;
            margin-left: 15px;
            vertical-align: middle;
            left: 8px;
            top: 25px;
            @include qodefTransition(width .2s .18s);
        }

        &:after {
            content: '';
            position: absolute;
            display: inline-block;
            border-top: 1px solid #fff;
            border-right: 1px solid #fff;
            width: 7px;
            height: 7px;
            top: 21.5px;
            left: 40px;
            transform: rotate(45deg) scale(0) translateY(-0.5px);
            @include qodefTransition(all .2s);
        }

        @include ipad-landscape {
            &:hover {
                padding: 7px 38px 7px 58px !important;
            }

            &:before {
                left: 3px;
                top: 26px;
            }
    
            &:after {
                top: 23px;
                left: 35px;
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Buttons styles - end
   ========================================================================== */