/* ==========================================================================
   WooCommerce Single Product Page styles - begin
   ========================================================================== */

.qodef-woo-single-page {
	
	&.qodef-woo-single-thumb-on-left-side {
		
		.qodef-single-product-content {
			
			.images {
				
				@media only screen and (min-width: $ipad-portrait-plus-pixel) {
					
					figure {
						width: 100%;
						margin: 0;
						overflow: hidden;
					}
					
					$thumb_size: 100;
					$space_between_items: 20;
					
					.woocommerce-product-gallery__image {
						
						&:first-child {
							$first_thumb_offset: $thumb_size + $space_between_items;
							
							left: #{$first_thumb_offset}px;
							width: calc(100% - #{$first_thumb_offset}px);
						}
					}
					
					&.woocommerce-product-gallery--with-images {
						
						.woocommerce-product-gallery__image {
							
							&:not(:first-child) {
								position: absolute;
								top: 0;
								left: 0;
								width: #{$thumb_size}px;
								padding: 0 0 #{$space_between_items}px;
								box-sizing: border-box;
								
								a {
									height: 100%;
									
									img {
										height: 100%;
									}
								}
							}
						}
					}
					
					$thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4');
					@for $i from 0 to length($thumb_3_columns_label) {
						&.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
							$column_value: $i+2;
							$column_size: 100 / $column_value;
							$column_width: calc(#{100% / $column_value} + #{$space_between_items / $column_value}px);
							
							.woocommerce-product-gallery__image {
								
								&:not(:first-child) {
									height: $column_width;
								}
								
								$thumbnail_numbers: (2, 3, 4, 5, 6, 7, 8, 9);
								@for $i from 0 to length($thumbnail_numbers) {
									$thumbnail_number_value: nth($thumbnail_numbers, $i+1);
									
									&:nth-child(#{$thumbnail_number_value}#{n}) {
										$first_param: ($column_size * $i)#{'%'};
										$second_param: ($space_between_items / $column_value * $i)#{px};
										$topPos: calc(#{$first_param} + #{$second_param});
										
										top: $topPos;
									}
								}
							}
						}
					}
				}
			}
		}
	}
    
    .product {
	    @include qodefRelativeHolderLayout();
	    float: none;
	    margin: 0;
    }
        
    .qodef-single-product-content {
        @include qodefRelativeHolderLayout();
        clear: both;
        margin: 0 0 20px;
	
	    .images {
		    position: relative;
		    display: inline-block;
		    vertical-align: middle;
		    width: 50%;
		    margin: 0;
		    padding: 0 29px 0 0;
		    float: left;
		    box-sizing: border-box;
		    
		    figure {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0 -10px;
			
			    a {
				    @include qodefRelativeHolderLayout();
				
				    img {
					    position: relative;
					    width: 100%;
					    display: block;
				    }
			    }
		    }
		
		    .woocommerce-product-gallery__trigger {
				position: absolute;
			    top: 10px;
			    right: 39px;
			    width: 30px;
			    height: 30px;
			    line-height: 30px;
			    text-align: center;
			    z-index: 10;
		    }
		    
		    .woocommerce-product-gallery__image {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0;
			    padding: 0 10px;
			    box-sizing: border-box;
			    
			    &:first-child {
				    left: 10px;
				    width: calc(100% - 20px);
				    padding: 0;
			    }
			
			    .zoomImg {
				    cursor: crosshair;
			    }
		    }
		    
		    &.woocommerce-product-gallery--with-images {
			
			    .woocommerce-product-gallery__image {
				
				    &:not(:first-child) {
					    float: left;
					    padding-top: 20px;
				    }
			    }
		    }
		
		    $thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4');
		    @for $i from 0 to length($thumb_3_columns_label) {
			    &.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
				    $column_width: 100%/($i+2);
				
				    .woocommerce-product-gallery__image {
					
					    &:not(:first-child) {
						    width: $column_width;
					    }
				    }
			    }
		    }
	    }
    }
	
    .qodef-single-product-summary {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        width: 50%;
        padding: 0 0 0 38px;
        float: left;
        box-sizing: border-box;
	
	    .qodef-single-product-title {
		    display: block;
		    margin: 0 0 9px;
			line-height: 42px;
	    }
	
	    .price {
		    @include qodefWooCommercePriceStyle();
			margin: 0;

			del,
			ins {
				font-size: 23px;
				line-height: 23px;
				text-decoration-color: #afafaf;

				.woocommerce-Price-amount {
					font-size: 16px;
					color: #afafaf;
				}
			}

			ins {

				.woocommerce-Price-amount {
					font-size: 23px;
					vertical-align: bottom;
					color: $default-heading-color;

					> span {
						vertical-align: bottom;
					}
				}
			}

	    }
	
	    .woocommerce-product-rating {
		    @include qodefRelativeHolderLayout();
		    line-height: 1;
			margin: 15px 0 3px 2px;

			a {

				&:hover {
					color: #969696;
				}
			}
	    }
	
	    .woocommerce-product-details__short-description {
		    @include qodefRelativeHolderLayout();
		    margin: 43px 0 0;
		
		    p {
			    margin: 0;
		    }
	    }
	
	    .product_meta {
		    margin: 90px 0 0;
		
		    > span {
			    display: block;
			    margin: 3px 0;
			
			    a {
				    color: $default-text-color;
				
				    &:hover {
					    color: $default-heading-color;
				    }
			    }
		    }
	    }
	
	    .qodef-woo-social-share-holder {
		    @include qodefRelativeHolderLayout();
		    margin: 0;
		
		    > * {
			    display: table-cell;
			    vertical-align: top;
		    }
		
		    > span {
			    padding: 0 9px 0 0;
		    }
	    }
	
	    p.stock.out-of-stock,
	    p.stock.in-stock {
		    color: $default-heading-color;
		    font-weight: bold;
		    margin: 18px 0 0;
	    }
	
	    form.cart {
		    @include qodefRelativeHolderLayout();
		    margin: 50px 0 0;
	    }
	
	    .qodef-quantity-buttons {
		    margin: 0 20px 0 0;
	    }

		button[type=submit]:not(.qodef-search-submit) {
			padding: 10px 48px;
		}
    }
	
	.woocommerce-tabs {
		@include qodefRelativeHolderLayout();
		margin: 100px 0 0;
		
		ul.tabs {
			@include qodefRelativeHolderLayout();
			list-style: none;
			margin: 0;
			border-bottom: 1px solid $default-border-color;
			text-align: center;
			clear: both;
			padding: 0 2000px;
			margin: 0 -2000px;
			
			> li {
				margin-bottom: -1px;
				position: relative;
				display: inline-block;
				vertical-align: middle;
				float: left;

				&:hover {
					border-bottom: 1px solid $default-heading-color;
				}
				
				a {
					position: relative;
					display: inline-block;
					vertical-align: top;
					padding: 21px 39px;
					font-size: 19px;
					font-weight: 700;

					&:hover {
						color: #000;
					}
				}

				&.active {
					border-bottom: 1px solid $default-heading-color;
				}
			}
		}
		
		.entry-content {
			@include qodefRelativeHolderLayout();
			
			> h2 {
				display: none;
			}
			
			> p {
				margin: 40px 0 20px;
				line-height: 25px;
			}
		}
		
		table {
			margin: 18px 0 20px;
			
			tr, th {
				border: 0;
			}
			
			td, th {
				padding: 3px 0;
				vertical-align: middle;
				text-align: left;
				
				p {
					margin: 0;
				}
			}
			
			th {
				width: 1%;
				padding: 0 10px 0 0;
				font-weight: 400;
				color: $default-text-color;
			}
			
			td {
				width: 99%;
			}
		}

		table.shop_attributes {
			margin: 40px 0 20px;
		}
		
		#reviews {
			@include qodefRelativeHolderLayout();
			margin: 40px 0 20px;
			
			h2 {
				font-size: 18px;
				line-height: 24px;
				margin: 0px 0 24px;
			}
			
			ol.commentlist {
				list-style: none;
				
				li {
					margin: 0 0 15px;
					
					&:last-child {
						margin: 0;
					}
				}
				
				.comment_container {
					@include qodefRelativeHolderLayout();
					
					img {
						float: left;
						width: 70px;
					}
				}
				
				.comment-text {
					padding: 0 0 0 90px;
					
					p {
						margin: 0;
					}
				}
			}
			
			.comment-respond {
				margin-top: 15px;
				
				.comment-reply-title {
					margin: 15px 0px;
					font-weight: 700;
					text-transform: uppercase;
					display: inline-block;
					color: $default-heading-color;

				}
				
				p {
					margin: 5px 0;
				}

				p.comment-form-author,
				p.comment-form-email {
					width: 48.8%;
					float: left;

					> input#email,
					> input#author {
						width: 100%;
						margin: 0 0 30px 0;
						padding: 0;
					}
				}

				p.comment-form-email {
					float: right;
				}

				p.comment-form-comment {

					textarea{
						border: 1px solid #969696;

						&:focus {
							border: 1px solid $default-heading-color;
						}
					}
				}

				p.form-submit {
					clear: both;
				}
				
				.stars {
					overflow: hidden;
					
					a {
						position: relative;
						display: inline-block;
						color: transparent;
						font-size: 0;
						margin: 0;
						@include qodefWooCommerceSingleProductRatingsStyle();

						&:before {
							line-height: 2.1em;
						}

						&.active:after {
							@supports (-ms-ime-align: auto) {
								width: 90px;
								bottom: 6px;
							}
						}
					}
				}
			}
		}
	}
	
	.related.products,
	.upsells.products {
		margin: 18px 0 0;
		
		> h2 {
			font-size: 35px;
			line-height: 30px;
			display: block;
			margin: 36px 0 36px;
			text-align: left;
		}
		
		.product {
			
			.qodef-pl-text-wrapper {
				margin: 20px 0 12px;
			}

			.qodef-pl-rating-holder {
				display: none;
			}
		}
	}
}
/* ==========================================================================
   WooCommerce Single Product Page styles - end
   ========================================================================== */