/* ==========================================================================
   Product List shortcode styles - begin
   ========================================================================== */

.qodef-pl-holder {
    @include qodefRelativeHolderLayout();
	padding: 10px 10px 0 10px;
	box-sizing: border-box;
	
	&.qodef-info-on-image {

		.qodef-pli {

			.qodef-pli-inner {

				.qodef-pli-text {
					background-color: rgba($default-background-color, 0.8);

					.qodef-pli-text-inner {

						.qodef-pli-title {
							margin: 7px 0;
						}

						.qodef-pli-rating-holder {
							display: none;
						}

						.qodef-pli-add-to-cart {
							margin: 0;

							.button {

								&.added {
									display: none;
								}
							}

							.button,
							.added_to_cart {
								@include qodefRelativeHolderLayout();
								@include qodefButtonSolidColor();
								width: auto;
								padding: 0;
								left: 0;
								@include qodefTransform(translateX(0));
								background-color: transparent;
								font-size: 18px;
								color: $default-heading-color;
								border: none;
								line-height: 16px;
								text-transform: lowercase;

								&:hover {
									background-color: transparent !important;
								}
							}

							.added_to_cart {

								&:after {
									content: '';
									position: relative;
									display: inline-block;
									vertical-align: middle;
									height: 1px;
									width: 42px;
									background-color: #b2b2b2;
									left: 8px;
									@include qodefTransformOrigin(left);
									@include qodefTransition(all .2s ease-in-out);
								}

								&:hover {

									&:after {
										@include qodefTransform(scaleX(0));
									}
								}
							}
						}
					}
				}
			}
		}
	}

    .qodef-pli {
        padding: 0;
        z-index: 100;

		.qodef-pli-inner {

			.qodef-pli-text-inner {
				text-align: left;
				padding: 0 23px;

				.qodef-pli-category {
					margin: 11px 0 3px;
					a {
						color: $default-text-color;
					}
				}

				.qodef-pli-excerpt {
					color: $default-heading-color;
					line-height: 1.3em;
					margin: 11px 0 3px;
				}

				.qodef-pli-rating-holder {
					@include qodefRelativeHolderLayout();
					line-height: 1;
					margin: 10px 0 3px;
				}

				.qodef-pli-rating {
					@include qodefWooCommerceRatingsStyle();
				}

				.qodef-pli-price {
					@include qodefWooCommercePriceStyle();
					margin: 30px 0 0;
					font-size: 18px;
					line-height: 1em;

					del {
						color: $default-text-color;
					}
				}
			}
		}
    }

    .qodef-pli-inner {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        overflow: hidden;

        &:hover {

			.qodef-pli-image {

				img {
					@include qodefTransform(scale(1.03, 1.03));
					@include qodefTransition(transform .5s ease-in);
				}
			}

            .qodef-pli-text {
                opacity: 1;
            }
        }

        .touch & {
            cursor: pointer;
        }

        .qodef-pli-image {
            @include qodefRelativeHolderLayout();

            img {
                display: block;
                width: 100%;
				@include qodefTransition(transform .5s ease-out);
				backface-visibility: hidden;
            }

            .qodef-pli-onsale,
            .qodef-pli-out-of-stock,
            .qodef-pli-new-product {
	            @include qodefWooCommerceMarksGeneralStyle();
            }

            .qodef-pli-onsale {
				margin: 16px 0;
				padding: 0 24px;
	            @include qodefWooCommerceSaleStyle();
            }

            .qodef-pli-out-of-stock {
				margin: 17px 0;
				padding: 0 22px;
	            @include qodefWooCommerceOutOfStockStyle();
            }
	
	        .qodef-pli-new-product {
				margin: 17px 0;
				padding: 0 25px;
		        @include qodefWooCommerceNewProductStyle();
	        }
        }
            
        .qodef-pli-text {
            @include qodefAbsoluteHolderLayout();
            opacity: 0;
            box-sizing: border-box;
            @include qodefTransition(opacity .4s ease-out);
        }

        .qodef-pli-text-outer {
	        @include qodefTableLayout();
	        text-align: center;
        }

        .qodef-pli-text-inner {
            @include qodefTableCellLayout();

            .qodef-pli-title,
            .qodef-pli-category a,
            .qodef-pli-excerpt,
            .qodef-pli-rating,
            .qodef-pli-price {
                color: $default-heading-color;
            }

			.qodef-pli-title a:hover {
				color: #969696;
			}
	
	        .qodef-pli-category a:hover {
		        color: $default-heading-color;
	        }

	        .qodef-pli-add-to-cart {
				margin: 13px 0 0;

		        &.qodef-default-skin {

			        .button,
			        .added_to_cart {
				        @include qodefButtonSolidColor();
						width: auto;
						position: absolute;
						bottom: 0;
						left: 50%;
						@include qodefTransform(translateX(-50%));
						padding: 7px 11px 6px;
						background-color: $default-heading-color;
						font-size: 14px;
						font-weight: bold;
						color: $default-background-color;
						border-bottom: 1px solid #858585;
						letter-spacing: 0.015em;

				        &:hover {
				            background-color: #262626 !important;
				        }
			        }
		        }

		        &.qodef-light-skin {

			        .button,
			        .added_to_cart {
				        color: $default-heading-color;
				        background-color: $default-background-color;

				        &:hover {
					        color: $default-background-color;
					        background-color: $first-main-color;
				        }
			        }
		        }
		
		        &.qodef-dark-skin {
			
			        .button,
			        .added_to_cart {
				        color: $default-background-color;
				        background-color: $default-heading-color;
				
				        &:hover {
					        color: $default-background-color;
					        background-color: $first-main-color;
				        }
			        }
		        }
	        }

            .button,
            .added_to_cart {
	            @include qodefButtonDefaultStyle();
	            @include qodefButtonSize();
	            white-space: nowrap;
	            z-index: 3;
            }

            .button.added {
                display: none;
            }
        }

	    .qodef-pli-link {
		    @include qodefAbsoluteHolderLayout();
	    }
    }

    .qodef-pli-text-wrapper {
        @include qodefRelativeHolderLayout();
        margin: 21px 0 20px;

		.qodef-pli-title {
			display: inline-block;
			color: $default-heading-color;
			margin: 0;

			a {
				&:hover {
					color: #969696;
				}
			}
		}

		.qodef-pli-price {
			position: absolute;
			margin: 5px 0 0 0;
			display: inline-block;
			right: 0;
			font-size: 15px;

			ins {
				text-decoration: none;
			}
		}

		.qodef-pli-category {
			margin: -1px 0 0 0;
			font-size: 15px;

			a {
				color: $default-text-color;

				&:hover {
					color: $default-heading-color;
				}
			}
		}
    }
	
	&.qodef-product-info-light {
		
		.qodef-pli-inner {
			
			.qodef-pli-text-inner {
				
				.qodef-pli-title,
				.qodef-pli-category a,
				.qodef-pli-excerpt,
				.qodef-pli-rating,
				.qodef-pli-price {
					color: #fff;
				}
				
				.qodef-pli-category a:hover {
					color: rgba(#fff, .8);
				}
			}
		}
	}
	
	&.qodef-product-info-dark {
		
		.qodef-pli-inner {
			
			.qodef-pli-text-inner {
				
				.qodef-pli-title,
				.qodef-pli-category a,
				.qodef-pli-excerpt,
				.qodef-pli-rating,
				.qodef-pli-price {
					color: $default-heading-color;
				}
				
				.qodef-pli-category a:hover {
					color: rgba($default-heading-color, .8);
				}
			}
		}
	}
	
	/*************** Masonry Layout - begin ***************/
	
	&.qodef-masonry-layout {
		box-shadow: 2px 3px 23px 0px rgba(0, 0, 0, 0.11);
		
		.qodef-pli-inner {
			width: 100%;
			height: 100%;

			.qodef-pli-image {
				height: 100%;
			}

			.qodef-pli-text-wrapper {
				@include qodefRelativeHolderLayout();
				margin: 22px 0 0;

				.qodef-pli-title {
					color: $default-heading-color;
					margin: 8px 0 0;
					line-height: 1em;
				}
			}
		}
	}
	
	/*************** Masonry Layout - end ***************/
}
/* ==========================================================================
   Product List shortcode styles - end
   ========================================================================== */