/* ==========================================================================
   WooCommerce Result and Ordering styles - begin
   ========================================================================== */
body.qodef-woocommerce-page {

    .select2-container--default {
        height: 31px;

        .select2-results__option {
            font-style: normal;
        }
    }

    .woocommerce-result-count,
    .woocommerce-ordering {
        display: inline;
        width: 50%;
        margin: 0 0 29px;
        float: left;
    }

    @include phone-portrait {

        .woocommerce-result-count,
        .woocommerce-ordering {
            display: block;
            width: 100%;
            float: none;
            text-align: left !important;
        }
    }

    .woocommerce-result-count {
        line-height: 31px;
    }

    .woocommerce-ordering {
        text-align: right;

        .orderby {
            width: auto;
            min-width: 250px;
            margin: 0;
            text-align: initial;
        }

        .select2-container--default {
            margin-bottom: 0;

            .select2-selection--single {
                border-top: none;
                border-right: none;
                border-bottom: 1px solid #bababa;
                border-left: none;
                height: 31px;
                line-height: 31px;

                .select2-selection__rendered {
                    padding: 0;
                    font-style: normal;
                    font-size: 16px;
                    line-height: 31px;
                    text-align: left;
                }

                .select2-selection__arrow {
                    border: none;
                    font-size: 12px;
                    width: 12px;
                    line-height: 31px;
                }
            }
        }
    }
}

/* ==========================================================================
   WooCommerce Result and Ordering styles - end
   ========================================================================== */