/* ==========================================================================
   WooCommerce Quantity Inputs styles - begin
   ========================================================================== */

.woocommerce-page .qodef-content .qodef-single-product-summary,
div.woocommerce {

    .qodef-quantity-buttons {
        border: 1px solid $default-border-color;
        margin: 0px 4px 0px 0px;
        padding: 5px 12px 5px 17px;
        position: relative;
        display: inline-block;
        vertical-align: middle;

        .qodef-quantity-label {
            padding-right: 46px;
        }

        .qodef-quantity-input,
        .qodef-quantity-minus,
        .qodef-quantity-plus {
            display: inline-block;
            vertical-align: middle;
            color: $default-text-color;
            background-color: transparent;
            text-align: center;
            -webkit-user-select: none;
            box-sizing: border-box;
        }

        input[type="text"].qodef-quantity-input {
            position: relative;
            height: 44px;
            width: 22px;
            padding: 0;
            margin: 0;
            font-family: inherit;
            line-height: 25px;
            outline: 0;
            font-size: 16px;
            border-bottom: none;
        }

        .qodef-quantity-minus,
        .qodef-quantity-plus {
            font-size: 12px;
            right: 0;
            width: 15px;
            height: 44px;
            line-height: 44px;
            border-left: 0;
            cursor: pointer;
            @include qodefTransition(color .15s ease-in-out);

            &:hover {
                color: $default-heading-color;
            }
        }

        .qodef-quantity-plus {
            top: 0;
            border-bottom: 0;
        }

        .qodef-quantity-minus {
            bottom: 0;
        }
    }
}

.woocommerce-page .qodef-content,
div.woocommerce {

    input[type="text"],
    input[type="email"],
    input[type="tel"],
    input[type="password"],
    textarea {
        @extend %input-style;
        border-top: none;
        border-right: none;
        border-bottom: 1px solid #bababa;
        border-left: none;
        padding: 12px 0px;

        &:focus {
            border-bottom: 1px solid $default-heading-color;
        }
    }

    .coupon {

        input[type="text"] {
            width: 200px;
        }
    }
}
/* ==========================================================================
   WooCommerce Quantity Inputs styles - end
   ========================================================================== */